import React, {useState}from 'react'
import Button from "react-bootstrap/Button";
import styled from "styled-components";
import Collapse from "react-bootstrap/Collapse";

const Styles = styled.div`
  .card {
    width: 100%;
    position: relative;
    z-index: -2;
  }
  
  .collapse-text {
    color: #222;
  }
`

function CollapseHistory() {
    const [open, setOpen] = useState(false);
    return (
        <Styles>
            <Button
                variant = "secondary"
                onClick={() => setOpen(!open)}
                aria-controls="collapse-text"
                aria-expanded={open}
            >
                {!open ? "История Лаборатории" : "История Лаборатории"}
            </Button> <br/><br/>
            <Collapse in={open}>
                <div id="collapse-text" className={"collapse-text"} align={"left"}>
                    <p> Летом 1966 г. В.С. Коптев-Дворников в связи с окончанием мною аспирантуры предложил мне работу (ассистент) на кафедре, но поставил некоторые условия. Одним из них было создание на кафедре лаборатории экспериментальной петрографии, а точнее - переоборудование и переориентация имеющейся лаборатории экспериментальной и технической петрографии. Ее создал при переезде факультета в новое здание Сергей Дмитриевич Четвериков. К тому времени в активе лаборатории было: создание и чтение двух курсов ("Физико-химические основы петрографии" и "Техническая петрография"), публикация двух учебников, активная работа с заводами, отраслевыми институтами по прикладным исследованиям, масса аспирантов и студентов. К Сергею Дмитриевичу и Анне Михайловне Батановой стремились люди с разных частей страны и из-за рубежа за советом, консультацией, с просьбами о помощи, стажировке сотрудников, приеме в аспирантуру на кафедру. Вопросы, которыми занимались Сергей Дмитриевич и Анне Михайловне, были живыми, актуальными, очень нужными, практическими. Надо сказать, что Bладимир Сергеевич угадал - эксперимент был давней моей мечтой еще со времени учебы в МГУ.</p>
                    <p>В.С. Коптев-Дворников предоставил мне в обеспечение своего задания комнату, штатную единицу старшего инженера, договорился в деканате о финансировании. В расчете на последующую работу лаборатории он организовал обучение трех студентов по индивидуальным планам (Жени Коптева-Дворникова, Тани Щекиной и Олега Луканина). До конца своих дней он интересовался состоянием дел по организации лаборатории и, чем мог, помогал.</p>
                    <p>Первые четыре года были довольно трудными. Для изготовления оборудования нужно было пройти всю цепочку: выбор аппарата - добывание технических чертежей - поиски изготовителя-добывание дефицитных материалов - изготовление - отладка - работа на установке. Стандартные приборы тоже было нелегко достать, их распределение строго лимитировалось. Приходилось много мотаться по соответствующим магазинам и отделам снабжения различных учреждений (в некоторых были "завалы" так называемых "неликвидов" - ненужного им оборудования, которое как раз было крайне необходимо нам). Всю эту работу приходилось совмещать с полной (до 1000 часов) учебной нагрузкой ассистента, научной работой, от которой мне самому не хотелось отказываться, договорными работами и общественной работой. Впрочем, организационную, снабженческую и чисто практическую работу слесаря, электрика со мной делил старший инженер Анатолий Александрович Тимофеев, без которого, конечно, никакой лаборатории и научных результатов у нас не было бы.</p>
                    <p>В 1970 г., придя на кафедру, А.А.Маракушев перевел меня на должность старшего научного сотрудника, и.о. зав. лабораторией. К этому времени мы уже начали ставить первые опыты на своих установках. С его приходом (а также В.А.Жарикова, который был зам. директора, а позже директором института экспериментальной минералогии) благодаря связям с институтом упростились проблемы снабжения, ремонта оборудования, в какой-то мере изготовления и монтажа. Стало возможным воспользоваться услугами соответствующих служб ИЭМ, помощью их механиков, лаборантов.</p>
                    <p>С 1973 г. после окончания аспирантуры в лаборатории работает Т.И.Щекина. С тех пор (и даже раньше - с ее аспирантуры) все успехи лаборатории мы делим с ней. В 1972 г. А.А.Маракушев поставил перед нами задачу организовать курс экспериментальной петрографии. В 1973 г. этот курс впервые был проведен. В следующем году мы организовали практику по экспериментальной петрографии в Черноголовке. В 1984 г. по курсу был издан учебник "Экспериментальная петрография". В 1994 г. в лабораторию пришел работать с экологической тематикой А.Р.Котельников, который участвует в чтении лекций по курсу, руководит курсовыми и дипломными работами, а также участвует в руководстве учебной практикой в Черноголовке. С 2007г. в лаборатории работает выпускница кафедры Я.О. Алферьева. В настоящее время ею готовится работа к защите кандидатской диссертации.</p>
                    <p>Нашими достижениями в эти годы являются следующие.</p>
                    <p>1) На основе равновесия раствор-кристалл показан разрыв смесимости в ряду твердых растворов шеелит-повеллит, изменение этого разрыва с температурой и исчезновение выше 500 С. Экспериментальные результаты использованы для интерпретации зональности и условий образования руд Тырныаузского и других скарновых Mo-W месторождений.</p>
                    <p>2) Фазовые отношения плавления и кристаллизации миаскитов Ильменогорского массива, являющегося одним из основных объектов Уральской петрографической практики.</p>
                    <p>3) Моделирование процессов пегматитообразования. Разработана модель образования пегматитового расплава путем отделения флюидного (силикатно-солевого), богатого фтором расплава.</p>
                    <p>4) Отделение флюидных расплавов во фторсодержащей гранитной системе и концентрирование рудного вещества в этих процессах. В этих работах, продолжающихся до сих пор, найдены подходы к решению одной из важнейших и сложных проблем эндогенной геологии - источников рудного вещества и специализации интрузий. Они открыли новые механизмы концентрации и рассеяния в геохимии. Эти работы были поддержаны грантами целого ряда приоритетных программ (Университеты России, РФФИ).</p>
                    <p>5) Моделирование процессов взаимодействия расплавов и горных пород (пироксенитов, долеритов, амфиболитов и гнейсов) - ассимиляции и магматического замещения. Работа была поддержана грантом РФФИ. Применительно к техногенному минералообразованию - это механизмы коррозии стенок огнеупорных контейнеров техногенными расплавами. Работы имели практическое значение и финансировались за счет договоров с производственными организациями.</p>
                    <p>6) Исследование важного тепло - и звукоизолирующего материала – базальтового волокна: его структуры, состава, в том числе валентного состояния Fe по данным Мессбауэровской спектроскопии, устойчивости воздействию температур и агрессивных водных растворов. Эти работы были поддержаны МКНТ и межфакультетскими грантами МГУ (с факультетом Наук о материалах, химическим и физическим факультетами, Музеем Землеведения)</p>
                    <p>В 1997 г, курсы "Экспериментальная петрография" и "Техническая петрография были объединены в один курс. В 2000 г. коллективом сотрудников опубликован учебник "Экспериментальная и техническая петрография", поддержанный специальной Федеральной целевой программой по изданию научной и учебной литературы в области фундаментальных наук. В 2003 г. издано Методическое руководство к занятиям по курсу «Экспериментальная и техническая петрология». В 2005 г. по материалам работ, выполненных в лаборатории, при поддержке РФФИ издана монография «Фазовые отношения во фторсодержащих гранитной и нефелин-сиенитовой системах и распределение элементов между фазами»</p>
                    <p>В марте 2012 года выходит из печати учебник  Е.Н. Граменицкого «Петрология метасоматических пород». </p>
                </div>
            </Collapse>
        </Styles>
    );
}

export const ExperLab = () => (
    <div>
        <h2>Лаборатория экспериментальной и технической петрологии</h2>
        <p>Это учебно-научная лаборатория, организованная профессором кафедры петрографии С.Д.Четвериковым в новом здании Университета на Ленинских горах в 1953 г. Ученым советом университета в 1994 г. Лаборатории присвоено имя ее основателя. В ней проводятся исследования по двум направлениям, тесно связанным между собой. Одно из них экспериментальная петрология, которое организовал доктор геолого-минералогических наук, профессор Е.Н.Граменицкий. С 1972 г. он стал заведующим лаборатории после кончины профессора С.Д.Четверикова. Целью проводимых по этому направлению исследований является выявление физико-химической сущности образования горных пород и руд. Лаборатория оснащена современным оборудованием, позволяющим проводить эксперименты в широком диапазоне температур и давлений, характерных для земной коры. </p>
        <p>Второе направление лаборатории - техническая петрография, которое возглавлял С.Д.Четвериков, а с 1972 г. ст.н.с., кандидат техн.наук А.М.Батанова. Это прикладное направление в петрографии, тесно связанное с современным материаловедением. Объектами исследования являются искусственные, главным образом силикатные технические камни, выпускаемые металлургической, химической, строительной и другими отраслями промышленности. Это металлургические шлаки, огнеупоры, керамика, стекло и ситаллы, каменное литье, цемент и др. Экспериментальные работы в этой области проводятся главным образом в "сухих" условиях в высокотемпературных печах. Петрографические исследования технических камней позволяют контролировать и совершенствовать технологические процессы. Технические камни по составу и структуре имеют большое сходство с горными породами. Поэтому данные, полученные по теоретическим и методическим вопросам в области технической петрографии, нередко проливают свет на природные процессы образования магматических и метаморфических горных пород.</p>
        <p>Лаборатория оказывает многочисленные консультации сотрудникам учебных, производственных и научно-исследовательских учреждений.</p>
        <p>На базе лаборатории организован и проводится объединенный (в 1998 г.) учебный курс "Экспериментальная и техническая петрология для студентов III курса, которые, наряду с прослушиванием курса лекций, также знакомятся с устройством и работой высокотемпературных печей, с автоклавами, с гидротермальной установкой высокого давления  (850 0 , 6000 кбар) и самостоятельно проводят опыты на этих установках. Кроме того, студенты проходят трехнедельную учебную практику в институте экспериментальной минералогии (ИЭМ) РАН.</p>
        <p>В лаборатории выполнялись курсовые, дипломные и диссертационные работы (в том числе иностранными учащимися).</p>
        <p>В лаборатории успешно трудятся старший научный сотрудник, кандидат геолого-минералогических наук Т.И.Щекина, ведущий научный сотрудник, доктор геолого-минералогических наук А.Р.Котельников, младший научный сотрудник Я.О.Алферьева, ст. инженер Т.А.Курбыко.</p>
        <p>В лаборатории защитили: докторские диссертацию – Е.Н. Граменицкий, В.Л. Сывороткин; кандидатские диссертации – Т.И.Щекина, А.М. Аксюк, Фам Тык Суан (Вьетнам), Н. Петков (Болгария), М. Перес (Куба), Н.Г. Зиновьева, Г.И. Крылова, О.В. Сибелев, В.Н. Девятова, Сюн Да-Хэ (Китай), Лебедева Р.В., Али Омар Абдель Азиз (Сирия), Гамбарян С.Г. (Армения), Бозаджиев Людмил Сл. (Болгария), Доброцветов , Х. Зигерт (Германия), Магди Махмуд Халиль (Египет), Мостафа Абдель Азиз (Египет), Н.В.Иванова, В.Фролова.</p>

        <a href='staff'>Сотрудники кафедры</a>
        <br/><br/>
        <CollapseHistory/>
    </div>
)
