import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { Home } from './pages/Home';
import About from './pages/About';
import { Abitu } from './pages/Abitu';
import { Science } from './pages/Science';
import { ExperLab } from './pages/ExperLab';
import { ZondLab } from './pages/ZondLab';
import { OptLab } from './pages/OptLab';
import { Staff } from './pages/Staff';
import { Study } from './pages/Study';
import { NoMatch } from './pages/NoMatch';
import { Layout } from './components/Layout';
import { NavigationBar } from './components/NavigationBar';
import {BottomBar} from "./components/BottomBar";
import { HomeJumbo, AboutJumbo, AbituJumbo, ScienceJumbo, StaffJumbo, StudyJumbo } from './components/Jumbotrons';

class App extends Component {
  render() {
    return (
      <React.Fragment>
        <Router>
          <NavigationBar />
          <Switch>
            <Route exact path="/" component={HomeJumbo}/>
            <Route path="/abitu" component={AbituJumbo}/>
            <Route path="/about" component={AboutJumbo}/>
            <Route path="/science" component={ScienceJumbo}/>
            <Route path="/experlab" component={ScienceJumbo}/>
            <Route path="/zondlab" component={ScienceJumbo}/>
            <Route path="/optlab" component={ScienceJumbo}/>
            <Route path="/staff" component={StaffJumbo}/>
            <Route path="/study" component={StudyJumbo}/>
          </Switch>
          <Layout>
            <Switch>
              <Route exact path="/" component={Home} />
              <Route path="/about" component={About} />
              <Route path="/abitu" component={Abitu} />
              <Route path="/science" component={Science} />
              <Route path="/experlab" component={ExperLab} />
              <Route path="/zondlab" component={ZondLab} />
              <Route path="/optlab" component={OptLab} />
              <Route path="/staff" component={Staff} />
              <Route path="/study" component={Study} />
              {/*<Route component={NoMatch} />*/}
            </Switch>
          </Layout>
            <br/><br/><br/>
            <BottomBar/>
        </Router>
      </React.Fragment>
    );
  }
}

export default App;
