function History() {
    return (
        <div className={'about'}>
                <h2> История кафедры </h2> <br/>

            <p> Начало школы петрографов в Московском университете было положено в 1911 г. приват-доцентом
                Н.Н.Смирновым, начавшим читать систематический курс петрографии на кафедре геологии. С этого года
                началась специализация студентов по петрографии. До 1917 г. по специальности петрография университет
                окончил А.С.Уклонский. Е.А. Кузнецов, И.Д. Пазилов , Л.П. Мамаев, И.В. Кларк, А.А. Левшин, Н.Р. Николаев
                и Б.В. Иванов.
                В 1917 г, была создана первая аспирантура по петрографии (аспиранты Е.А. Кузнецов, И.Д. Пазилов и Л.П.
                Мамаев). В 1920 г. по декрету Совнаркома в Московском университете была организована кафедра
                петрографии, заведовать которой стал вновь избранный профессор Н.Н.Смирнов, только что защитивший
                докторскую диссертацию. В штат новообразованной кафедры вошли кроме профессоров Н.Н.Смирнова и
                К.И.Висконта, ассистенты С.Д.Четвериков, Е.А.Кузнецов, В.А.Гедройц, Ю.А.Хмелева и Л.П.Мамаев. Профессор
                Н.Н.Смирнов помимо общего курса петрографии начал читать также курсы полезных ископаемых и
                петрографических провинций.
                В 1925 г. на кафедре петрографии МГУ был впервые введен курс Федоровского столика, который вел доцент
                Е.А. Кузнецов. Профессор К.И.Висконт с ассистентом Ю.А. Хмелевой организовал курс определения горных
                пород по микроскопическим признакам. Кроме этого К.И. Висконт вел курсы петрологии и физико-химические
                основы петрологии, а в течение нескольких лет и курс полевой петрологии. Профессор Н. Н. Смирнов наряду
                с Д.С.Белянкиным был одним из пионеров технической петрографи
                у нас в стране. Много внимания он уделял изучению строительных материалов, что послужило основой его
                монографии ''Петрографические исследования в подмосковном карбоне" и ряда статей по изучению огнеупоров,
                стекла, керамики и других технических камней. Вместе с тем он вел исследования в области петрографии
                Ильменского заповедника и Армении, по результатам которых им был опубликован целый ряд работ.
                В 1929-1930 г.г. произошло слияние кафедры петрографии Московского университета и Московской горной
                академии в стенах МГРИ под руководством профессора В.И.Лучицкого. Преподавание петрографии, как и других
                геологических дисциплин, в МГУ было ликвидировано. Остался только небольшой вспомогательный курс при
                кафедре геологии на Географическом факультете. Заведовал этой кафедрой профессор А.Н.Мазарович, а курс
                проводил доцент С.Д.Четвериков. В 1938 г. на эту кафедру на должность профессора был приглашен
                Е.А.Кузнецов, который через год возглавил вновь образованную в МГУ кафедру петрографии. С этого времени
                в составе кафедры были доцент а затем профессор С.Д.Четвериков, ассистенты Н.Н.Ормонт, А.И.Волженков и
                позднее Г.А.Рашин. Научные работы и подготовка кадров на кафедре в это время проводились как по линии
                магматической и метаморфической петрологии, так и по технической петрологии.
            </p>
            <CardDeck>
                <Card border="light" style={{width: "25rem"}}>
                    <Card.Img variant="top" src={hist1}/>
                    <Card.Body>
                        <Card.Text>
                            На фото слева направо:
                            Демин А.М., Четвериков С.Д., Коптев-Дворников В.С., Ракчеев А.Д., Граменицкий Е.Н.,
                            Феногенов А.Н., Емельянинко П.Ф.
                        </Card.Text>
                    </Card.Body>
                </Card>
                <Card border="light" style={{}}>
                    <Card.Img variant="top" src={hist2}/>
                    <Card.Body>
                        <Card.Text>
                            На фото слева направо:
                            Емельяненко П.Ф., Ормонт Н.Н., Фролова Т.И., Кузнецов Е.А., Федоров Т.О., Феногенов А.Н.
                        </Card.Text>
                    </Card.Body>
                </Card>
            </CardDeck>
            <p> После переезда в высотное здание МГУ на Ленинских горах в 1953 г. кафедра петрографии получила новые
                помещения и развернула научно-исследовательские работы на всей территории страны по главной проблеме -
                магматические и метаморфические формации и связанная с ними рудоносность. Кафедра ведет большие
                хоздоговорные работы на Урале (исследования Е.А.Кузнецова, Т.И.Фроловой, А.Д.Ракчеева, А.Н.Феногенова,
                В.С. Шарфмана, Г.Б.Рудника и др.), в Казахстане (работы В.С.Коптева-Дворникова, П.Ф.Емельяненко,
                В.И.Фельдмана и др.), на Кавказе (А.М.Демин, А.В.Гущин, М.А.Петрова и др.), на Алтае (Е.И.Венцловайте,
                Е.Б.Яковлева и др.) Состав кафедры усилился профессором В.С. Коптевым-Дворниковым, возглавившим кафедру
                в 1963 г. Из новых кадров выросли преподаватели Т.И.Фролова, П.Ф.Емельяненко, А.Д.Ракчеев, Р.Н.Соболев,
                А.Н.Феногенов и др. В новом здании на кафедре стала функционировать лаборатория экспериментальной и
                технической петрографии во главе с С.Д.Четвериковым. Под его руководством и старшего научного сотрудника
                А.М.Батановой проводились исследования по госбюджетной и хоздоговорной тематике по технической
                петрографии, а также студенческие курсовые и дипломные работы и диссертационные работы аспирантов со
                значительной консультативной работой в этой области. В 1966 г. по инициативе В.С. Коптева-Дворникова в
                лаборатории создается экспериментальное направление, целью которого явилось выявление физико-химической
                сущности образования горных пород и руд. Оснащение оборудованием и научное руководство было поручено
                закончившему тогда аспирантуру кафедры Е.Н.Граменицкому. Им совместно с научным сотрудником Т.И.Щекиной
                проводятся эксперименты в широком интервале температур и давлений, характерных для земной коры и верхней
                мантии. В исследованиях принимают участие студенты и аспиранты. После кончины С.Д. Четверикова в 1972 г.
                Е.Н. Граменицкий, будучи уже доктором геолого-минералогических наук, стал заведующим лабораторией
                экспериментальной и технической петрографии.
                В 1954 г. положено начало методических работ по изучению физических свойств минералов и горных пород
                (А.Д. Ракчеев и Е.А. Кузнецов) и был организован кабинет специальных оптических методов исследования.
                При кафедре была организована докторантура и выпущены первые доктора наук (В.Р.Надирадзе и
                А.М.Даминова).
                В новом здании университета при активном участии кафедры на факультете была открыта шлифовальная
                лаборатория, которую возглавила и многие годы продолжает успешно руководить Вера Алексеевна Кондратьева.
                В 1965 г. организуется лаборатория физических методов исследования минералов и горных пород во главе с
                А.Д. Ракчеевым, а через год на базе хоздоговорных работ - лаборатория палеовулканологии во главе с
                В.С.Коптевым-Дворниковым, а с 1970 г. - Т.И.Фроловой.
                В 1970 г. состав кафедры пополнился профессорами А.А.Маракушевым, который возглавил кафедру,
                В.А.Жариковым и Л.Л.Перчуком. С этого времени на кафедре стало развиваться направление
                физико-химического анализа парагенезисов минералов и экспериментальной петрологии. Одновременно
                усилились связи с рядом научно-исследовательских институтов АН СССР, в которых студенты и аспиранты
                кафедры получили возможность проводить свои исследовательские работы. В развитии петрологии особенно
                важную роль сыграло сотрудничество с Институтом экспериментальной минералогии (ИЭМ) АН СССР.
                В 1990 г. кафедру возглавил Л.Л. Перчук, а в 1991 г. кафедра была переименована в кафедру петрологии.

                С 2009 года кафедру петрологии возглавляет А.Л.Перчук. 1 июля 2019 года кафедра переименована в кафедру
                петрологии и вулканологии.
            </p>
            <CollapseLinks/>
            </div>
    )
}

